:root {
    --bg-dark: #161512;
    --color-primary: #262421;
    --color-secondary: #302E2C;
    --color-text: #bababa
}

body {
    background: var(--bg-dark);
    background: #161512 linear-gradient(to bottom, #2e2a24, #161512 116px) no-repeat;
    color: var(--color-text);
}

.header {
    display: flex;
    padding: 1rem;
}

.logo {
    font-size: x-large;
}

.header nav{
    flex: 1;
    display: flex;
}

.header nav ul{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header nav li{
    list-style: none;
    padding: 1rem;
}

.chess-wrap {
    width: 100%;
    height: 100%;
}

.chess {
    padding: 1rem;
    display: grid;
    grid-template-columns: minmax(230px, 20vw) minmax(calc(70vmin * var(--board-scale)), calc(100vh * var(--board-scale) - calc(var(--site-header-height) + var(--site-header-margin)) - 3rem)) minmax(240px, 400px);
    grid-template-rows: fit-content(0);
    grid-template-areas:
        'side board details'
        'uchat under .';
    width: 70%;
    height: 100%;
    grid-gap: 2vmin;
}

.chess-chat {
    grid-area: side;
}

.chess-win-lose-panel {
    grid-area: under;
}

.chess-board {
    grid-area: board;
}

.chess-round-details {
    grid-area: details;
}

.logo, .chess-wrap, .chess-chat-wrap, .chess-win-lose-panel, .chess-board, .chess-round-details  {
    display: flex;
    justify-content: center;
    align-items: center;
}

.chess-chat, .chess-win-lose-panel, .chess-board, .chess-round-details  {
    background: var(--color-primary);
}

.chess-board {
    padding: 1rem;
}

.chess-chat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.chess-chat input {
    border: none;
    background: transparent;
}